.detailInfo{
    display: flex;
    flex-direction: column;
    justify-content: center;
    h3{
        padding-bottom: 2rem;
    }
    >p{
        padding-bottom: 1rem;
    }
    .price{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        background: #f1f1f1;
        padding: 2rem;
        border-radius: 5px;
        font-size: 1.25rem;
        .count {
            width: 20%;
            display: flex;
            justify-content: space-between;
            background-color: #fff;
            border-radius: 10px;
            button{
                padding: 0 10px;
                border: none;
                background-color: inherit;
                border-radius: 5px;
            }
        }
    }
    .check{
        display: flex;
        align-items: center;
        input{
            border: 1px solid #000;
            width: 5rem;
            text-align: center;
        }
        button{
            margin-right: 1rem;
        }
    }
}