li{
    list-style: none;
}
.App{
    width: 100vw;
    height: auto;
    .header{
        line-height: 3rem;
        margin-bottom: 3rem;
    }
    .mainBanner{
        width: 100%;
        height: 40vh;
        background: url("./img/main/mainbanner1.jpg") no-repeat center center;
        background-size: cover;
        color: #fff;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .mainitemList{
        max-width: 80%;
        height: auto;
        margin: 0 auto;
        margin-bottom: 3rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        h1{
            padding-top: 3rem;
            padding-bottom: 3rem;
            font-size: 2rem;
        }
        ul{
            width: 100%;
            height: auto;
            display: flex;
            align-items: center;
            justify-content: space-between;
            .mainItems{
                width: 18%;
                height: 100%;
                padding: 10px;
                border: 1px solid #ccc;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                .mainImg{
                    width: 100%;
                    height: 100%;
                    img{
                        width: 100%;
                        height: 100%;
                    }
                }
            }
            .mainItemsInfo{
                height: auto;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                h2{
                    padding-top: 1.5rem;
                    font-size: 1.25rem;
                }
                p{
                    text-decoration: line-through;
                    text-decoration-color: #aaa;
                    color: #ccc;
                }
                h4{
                    font-size: 1rem;
                }
            }
        }
    }
    .eventbanner{
        display: flex;
        flex-direction: column;
        align-items: center;
        max-width: 80%;
        margin: 0 auto;
        margin-bottom: 100px;
        h2{
            padding-top: 3rem;
            padding-bottom: 2rem;
            font-size: 2rem;
        }
        ul{
            width: 100%;
            height: 20vh;
            display: flex;
            justify-content: space-between;
            .eventImgs{
                width: 49%;
                border-radius: 10px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                padding: 2rem;
                h4{
                    font-weight: bold;
                    font-size: 1.25rem;
                    line-height: 1.5;
                    padding-bottom: 1rem;
                }
                p{
                    font-size: 1rem;
                }
            }
            .eventImgs1 {
                background: url('./img/main/eventbanner1.jpg') center center/cover no-repeat;
            }
            .eventImgs2 {
                background: url('./img/main/eventbanner2.jpg') center center/cover no-repeat;
            }
        }
    }
    .best{
        .BeforePrice{
            display: none;
        }
    }
}